.row {
	display: grid;
	grid-template-columns: repeat(12, 1fr);
	grid-gap: 10px;
	grid-auto-flow: row;
	margin: 0 auto;
}

.column {
	order: 1;
}

.column-1 {
	grid-column: span 1;
}

.column-3 {
	grid-column: span 3;
}

.column-4 {
	grid-column: span 4;
}

.column-6 {
	grid-column: span 6;
}

.column-10 {
	grid-column: span 10;
}

.column-12 {
	grid-column: span 12;
}
